.game {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-top: 100px;
  width: 750px;
}
.game .board .square {
  color: darkcyan;
  background: #FFF;
  border: 10px solid darkcyan;
  width: 150px;
  height: 150px;
  float: left;
  font-size: 100px;
  line-height: 34px;
  margin-right: -10px;
  margin-top: -10px;
  padding: 0;
  text-align: center;
}
.game .board .square:focus {
  outline: none;
}
.game .board .board-row:after {
  clear: both;
  content: "";
  display: table;
}
.game .status {
  color: darkgoldenrod;
  font-weight: bold;
  margin-left: 20px;
}
.game .game-info {
  color: darkgoldenrod;
  margin-left: 20px;
  width: 200px;
}
.game .game-info a {
  color: darkgoldenrod;
}
